<template>
<div>
  <div class="information" v-for="(item,index) in usermsg" :key="index">
      <!-- header部分 -->
    <header>
        <div class="h_top">
            <span>{{item.name}}</span>
            <span>{{item.company}}</span>
        </div>
        <div class="h_middle">
            <span>社保账号</span>
            <span>参保所在地</span>
        </div>
        <div class="h_bottom">
            <span>{{item.account}}</span>
            <span>{{item.securityLocation}}</span>
        </div>
    </header>
    <!-- 内容区域 -->
    <div class="main">
        <ul>
            <li>
                <router-link to="/security_detail" href="#">
                    <div class="left">
                        <img src="../assets/treatment.png" alt="">
                        <span>职工医疗保险</span>
                    </div>
                    <div class="right">
                        <span>余额</span>
                        <span>{{item.empMedicalInsurance}}</span>
                    </div>
                </router-link>
            </li>
            <li>
                <router-link to="/oldpension" href="#">
                    <div class="left">
                        <img src="../assets/pension.png" alt="">
                        <span>企业养老保险</span>
                    </div>
                    <div class="right">
                        <span>2017年8月</span>
                        <span>{{item.companyOldInsurance}}</span>
                    </div>
                </router-link>
            </li>
            <li>
                <router-link to="/injury" href="#">
                    <div class="left">
                        <img src="../assets/injury.png" alt="">
                        <span>工伤保险</span>
                    </div>
                    <div class="right">
                        <span>2017年8月</span>
                        <span>{{item.jobInjuredInsurance}}</span>
                    </div>
                </router-link>
            </li>
            <li>
                <router-link to="/birth" href="#">
                    <div class="left">
                        <img src="../assets/birth.png" alt="">
                        <span>生育保险</span>
                    </div>
                    <div class="right">
                        <span>2017年8月</span>
                        <span>{{item.maternityInsurance}}</span>
                    </div>
                </router-link>
            </li>
            <li>
                <router-link to="/losejob" href="#">
                    <div class="left">
                        <img src="../assets/unemployment.png" alt="">
                        <span>失业保险</span>
                    </div>
                    <div class="right">
                        <span>2017年8月</span>
                        <span>{{item.losejobInsurance}}</span>
                    </div>
                </router-link>
            </li>
        </ul>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'information',
  data () {
    return {
      msg: '我是社保信息页面',
      usermsg:''
    }
  },
created () {
    // var city = this.$store.city
    var city = localStorage.getItem("city")
    
    // var name=   this.$store.idnumber
    // var id= this.$store.password
    var realname = localStorage.getItem("realname")
    var cardid = localStorage.getItem("cardid")

     this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/socialsecurity.do', {
      params: {
          location:city,
        //   idnumber:realname,
        //   password:cardid

          idnumber:"张三",
          password:"159357"
          } 
        })
          .then( res => {
            var data = res.data
            this.usermsg = data
            // console.log(this.usermsg)
            localStorage.removeItem("realname")
            localStorage.removeItem("cardid")
        })
          .catch(function (error) {
          console.log(error);
        });
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
    /* header 部分 */
    header {
        width: 100%;
        height: 136px;
        background: url(../assets/ss-bg.png) no-repeat;
        background-size: 100%;
        padding: 15px 10px 0px 10px;
        box-sizing: border-box;
    }
    header .h_top {
        margin-bottom: 30px;
        color: #fff;
        font-size: 14px;
    }
    header .h_top span:nth-of-type(1) {
        border-right: 1px solid #fff;
        padding: 0 10px 0 0;
    }
    header .h_top span:nth-of-type(2) {
        padding: 0 0 0 10px;
    }
    header .h_middle {
        color: rgba(255,255,255,.7);
        font-size: 14px;
        margin-bottom: 10px;
    }
    header .h_middle span:nth-of-type(2) {
        float: right;
    }
    header .h_bottom {
        color: #fff;
        font-size: 14px;
    }
    header .h_bottom span:nth-of-type(2) {
        float: right;
    }
    /* 内容区域 */
    .main {
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
    }
    .main ul {
        width: 100%;
        margin: 0;
    }
    .main ul li {
        width: 100%;
        height: 60px;
        margin: 10px 0 20px 0;
        border-bottom: 1px solid #e7e7e7;
    }
    .main ul li a {
        display: inline-block;
        width: 100%;
    }
    .main ul li .left {
        float: left;
        position: relative;
    }
    .main ul li .left img {
        width: 25px;
        height: 25px;
        position: absolute;
        top: 10px;
    }
    .main ul li .left span {
        color: #333;
        font-size: 16px;
        line-height: 44px;
        margin-left: 35px;
    }
    .main ul li .right {
        float: right;
    }
    .main ul li .right span {
        color: #333;
        font-size: 15px;
        display: block;
        text-align: right;
        margin: 5px;
    }
    .main ul li .right span:nth-of-type(2) {
        color: #fed517;
    }
</style>



