<template>
<div>
  <div class="security_detail"  v-for="(item,index) in usermsg" :key="index">
      <!-- header部分 -->
    <header>
        <div class="h_content">
            <div class="h_content_left">
                <div id="int_result" v-show="isshow">{{item.balance}}</div>
                <div id="hide" v-show="isshow2">****</div>
                <img src="../assets/open.png" alt="" v-show="isshow" @click="toggle()">
                <img src="../assets/close.png" alt="" v-show="isshow2" @click="toggle()">
                <div class="balance">账户余额(元)</div>
            </div>
            <div class="h_content_right">
                <span>{{item.monthes}}</span>
                <span>已缴纳月数</span>
            </div>
        </div>
    </header>
    <!-- 缴存明细与提取明细 -->
    <div class="nav">
        <mt-navbar v-model="selected">
        <mt-tab-item id="1">缴存明细</mt-tab-item>
        <mt-tab-item id="2">提取明细</mt-tab-item>
    </mt-navbar>
<!-- tab-container -->
    <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
            <div class="newsview">
                <ul class="resultList">
                    <li>
                        <div class="resultTop">
                        <h4>{{item.when}}</h4>
                        <div class="resultinfo">
                            <ul>
                            <li>
                                <span>{{item.personerPay}}</span>
                                <span>个人缴纳</span>
                            </li>
                            <li>
                                <span>{{item.companyPay}}</span>
                                <span>单位缴纳</span>
                            </li>
                            <li>
                                <span>{{item.sum}}</span>
                                <span>合计（元）</span>
                            </li>
                            </ul>
                        </div>
                        </div>
                        <div class="resultBottom" style="display:none;">
                        <div class="resultBottom1">
                            <span>缴纳基数</span>
                            <span>{{item.basePay}}</span>
                        </div>
                        <div class="resultBottom2">
                            <span>参保单位</span>
                            <span>{{item.participatingUnits}}</span>
                        </div>
                        </div>
                        <div class="_show">
                        <img class="showimg" src="../assets/down.png" alt="">
                        </div>
                    </li>
                </ul>
            </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2" v-for="(items,index) in usermsg2" :key="index">
            <div class="datal">
                <table class="datallist" width="100%" cellspacing="0px" style="display:table;">
                    <tbody>
                        <tr height="30px" class="font_bgc font14">
                            <td width="25%" align="center">操作时间</td>
                            <td width="25%" align="center">操作类型</td>
                            <td width="25%" align="center">操作地点</td>
                            <td width="25%" align="center">金额</td>
                        </tr>
                        <tr height="40px" class="fontsize">
                            <td width="25%" align="center" class="color9">{{items.operateDate}}</td>
                            <td width="25%" align="center" class="color58e">{{items.operateType}}</td>
                            <td width="25%" align="center" class="colorgreen">{{items.operatePlace}}</td>
                            <td width="25%" align="center" class="colorred">{{items.money}}</td>
                        </tr>
                        <tr height="40px" class="font_bgc">
                            <td width="25%" align="center" class="color9">{{items.operateDate}}</td>
                            <td width="25%" align="center" class="color58e">{{items.operateType}}</td>
                            <td width="25%" align="center" class="colorgreen">{{items.operatePlace}}</td>
                            <td width="25%" align="center" class="colorred">{{items.money}}</td>
                        </tr>
                        <tr height="40px" class="fontsize">
                            <td width="25%" align="center" class="color9">{{items.operateDate}}</td>
                            <td width="25%" align="center" class="color58e">{{items.operateType}}</td>
                            <td width="25%" align="center" class="colorgreen">{{items.operatePlace}}</td>
                            <td width="25%" align="center" class="colorred">{{items.money}}</td>
                        </tr>
                        <tr height="40px" class="font_bgc">
                            <td width="25%" align="center" class="color9">{{items.operateDate}}</td>
                            <td width="25%" align="center" class="color58e">{{items.operateType}}</td>
                            <td width="25%" align="center" class="colorgreen">{{items.operatePlace}}</td>
                            <td width="25%" align="center" class="colorred">{{items.money}}</td>
                        </tr>
                    </tbody>
                </table>
                
            </div>
        </mt-tab-container-item>
    </mt-tab-container>
    </div>
</div>
</div>
</template>

<script>
import '../lib/js/jquery-1.11.0.min.js'
$(function(){
  $(document).on("click","._show",function(){
      if(!$(this).children().hasClass('toggleimg')) {
        $(this).children().addClass('toggleimg')
        $(this).prev().css({
          "display":"block"
        });
      }else {
        $(this).children().removeClass('toggleimg')
        $(this).prev().css({
          "display":"none"
        });
      }
  })
})
import Vue from 'vue'
import { Navbar, TabItem } from 'mint-ui';
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);

export default {
  name: 'security_detail',
  data () {
    return {
      msg: '社保明细',
      isshow:true,
      isshow2:false,
      selected:'1',
      show:false,
      usermsg:'',
      usermsg2:''
    }
  },
  methods:{
        toggle() {
            this.isshow = !this.isshow
            this.isshow2 = !this.isshow2
        },
    },
    created(){
        // var loginname = this.$store.password 
        // var idnumber = this.$store.idnumber
    var realname = localStorage.getItem("realname")
    var cardid = localStorage.getItem("cardid")
    //http://jmpotal.hanweb.com/jmp
    this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/empmedicalinsurancestore.do', {
        params: {
            idnumber:"159357",
            name:"张三",//这里传登录过后的用户名

            // idnumber:cardid,
            // name:realname,//这里传登录过后的用户名
            } 
        })
          .then( res => {
            var data = res.data
            this.usermsg = data
        })
          .catch(function (error) {
          console.log(error);
        });
        this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/empmedicalinsuranceget.do', {
        params: {
            // idnumber:idnumber,
            // name:loginname,//这里传登录过后的用户名

            idnumber:"159357",
            name:"张三",//这里传登录过后的用户名
            } 
        })
          .then( res => {
            var data = res.data
            this.usermsg2 = data
            localStorage.removeItem("realname")
            localStorage.removeItem("cardid")
        })
          .catch(function (error) {
          console.log(error);
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
    /* header 部分 */
    header {
        width: 100%;
        height: 136px;
        background: url(../assets/ss-bg.png) no-repeat;
        background-size: 100%;
        padding: 15px 10px 0px 10px;
        box-sizing: border-box;
    }
    .h_content {
        box-sizing: border-box;
        width: 100%;
        padding: 10px 10px 30px;
        color: #fff;
    }
    .h_content_left {
        width: 50%;
        position: relative;
        border-right: 1px solid #fff;
        margin: 0;
        float: left;
    }
    .h_content img {
        width: 25px;
        height: 25px;
        position: absolute;
        top: 6%;
        right: 6%;
    }
    #int_result {
        width: 60px;
        height: 40px;
        box-sizing: border-box;
        font-size: 22px;
    }
    #hide {
        font-size: 35px;
        text-align: left;
        width: 60px;
        height: 40px;
    }
    .balance {
        font-size: 14px;
        color: rgba(255, 255, 255, .7);
    }
    .h_content_right {
        width: 45%;
        float: right;
        text-align: center;
        float: right;
    }
    .h_content_right span {
        display: block;
        margin: 5px 0 10px 0;
    }
    .h_content_right span:nth-of-type(1) {
        font-size: 22px;
    }
    .h_content_right span:nth-of-type(2) {
        font-size: 14px;
        color: rgba(255, 255, 255, .7);
    }
    /* 缴存明细与提取明细 导航*/
    .nav {
        padding: 10px;
        margin-top: 30px;
    }
    .mint-navbar {
        height: 34px;
    }
    .mint-tab-item {
        border-right: 1px solid #00558e!important;
        line-height: 30px;
        border: 1px solid #00558e;
        width: 50%;
        height: 30px;
        padding: 0;
        color: #00558e;
        /* border-radius: 2.5px; */
    }
    /* 缴存明细分页 */
    .newsview {
        padding: 2px;
    }
    .newsview .resultList {
        width: 100%;
        box-sizing: border-box;
    }
    .newsview .resultList li {
        margin: 0;
        width: 93%;
        border: 1px solid #e7e7e7;
        padding: 10px;
    }
    .resultTop h4 {
        margin-top: 0;
        font-weight: 100;
        font-size: 15px;
    }
    .resultinfo {
        overflow: hidden;
    }
    .resultTop .resultinfo ul li span:nth-of-type(1) {
        color: #FED517;
    }
    .resultTop .resultinfo ul li {
        width: 32%;
        float: left;
        border: none;
        text-align: center;
        font-size: 14px;
        color: #999;
        padding: 0;
        -webkit-tap-highlight-color:rgba(255,255,255,0);
    }
    .resultTop .resultinfo ul li span {
        display: block;
        margin: 10px 0 5px 0;
    }
    .resultBottom {
        border-top: 1px solid #e7e7e7;
        margin-top: 10px;
    }
    .resultBottom span {
        font-size: 14px;
        color: #999;
        display: inline-block;
        margin: 6px 0 6px 0;
    }
    
    .resultBottom .resultBottom1 {
        margin: 6px 0 6px 0;
    }
    .resultBottom .resultBottom2 {
        margin: 6px 0 6px 0;
    }
    .resultBottom .resultBottom1 span:nth-of-type(2) {
        float: right;
    }
    .resultBottom .resultBottom2 span:nth-of-type(2) {
        float: right;
    }
    ._show {
        width: 100%;
        height: 16px;
    }
    ._show img {
        display: block;
        width: 16px;
        height: 16px;
        margin: 0 auto;
    }
    .toggleimg {
        transform: rotate(180deg)
    }
    /* 提取明细 */
    .datal {
        margin-top: 10px;
    }
    .font_bgc {
        font-size: 12px;
        background-color: #f2f2f2;
    }
    .font14 {
        font-size: 14px;
    }
    .fontsize {
        font-size: 12px;
    }
    .color9 {
        color: #999;
    }
    .color58e {
        color: #00558e;
    }
    .colorgreen {
        color: #259b24;
    }
    .colorred {
        color: red;
    }
    
</style>



