<template>
  <div id="app" v-if="isRouterAlive">
    
    <router-view/>
  </div>
</template>

<script>
import './lib/js/jquery-1.11.0.min.js'
// 这是project中每个组件共同的点击显示隐藏方法
$(document).ready(function(){
    $(document).on("click",".show",function(){
      if(!$(this).children().hasClass('toggleimg')) {
        $(this).children().addClass('toggleimg')
        $(this).prev().css({
          "display":"block"
        });
      }else {
        $(this).children().removeClass('toggleimg')
        $(this).prev().css({
          "display":"none"
        });
      }
  })
})
export default {
  name: 'App',
  provide(){
    return{
      reload:this.reload
    }
  },
  data(){
    return{
      isRouterAlive:true
    }
  },
  methods:{
    reload(){
      this.isRouterAlive = false
      this.$nextTick(function(){
        this.isRouterAlive = true
      })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  /* padding-top: 60px; */
}
html {
  background-color: #fff;
}
body {
  margin: 0;
  background-color: #fff;
}
.footer_fixed {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: #f8f8f8;
  position: fixed;
  bottom: 0;
  font-size: 12px;
  color: #999;
}
</style>



