import Vue from 'vue'
import Router from 'vue-router'

import query from '../project/query.vue'
import information from '../project/information.vue'
import security_detail from '../project/security_detail.vue'
import oldpension from '../project/oldpension.vue'
import injury from '../project/injury.vue'
import birth from '../project/birth.vue'
import losejob from '../project/losejob.vue'


Vue.use(Router)

export default new Router({
  model:history,
  routes: [
    {
      path: '/',
      name: 'query',
      component: query
    },
    {
      path: '/query',
      name: 'query',
      component: query
    },
    {
      path: '/information',
      name: 'information',
      component: information
    },
    {
      path: '/security_detail',
      name: 'security_detail',
      component: security_detail
    },
    {
      path: '/oldpension',
      name: 'oldpension',
      component: oldpension
    },
    {
      path: '/injury',
      name: 'injury',
      component: injury
    },
    {
      path: '/birth',
      name: 'birth',
      component: birth
    },
    {
      path: '/losejob',
      name: 'losejob',
      component: losejob
    },
    
  ]
})



