import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
var code = localStorage.getItem("code")
const state = {
    count:1,
    city:'',
    loginname:'李庆',
    idnumber:'',
    password:''
}
// const getters = {
//     count:function(state){
//         return state.count +=100;
//     }
// }
export default new Vuex.Store({
    state, // 组件中的data
    // getters,// 组件中的mounted
    mutations:{ // 组件中的methods 
        add(state) {
            state.count+=1;
        },
        dele(state) {
            state.count-=1;
        }
    }
});


