<template>
  <div class="losejob">
       <!-- 缴存明细与提取明细 -->
    <div class="nav">
        <mt-navbar v-model="selected">
        <mt-tab-item id="1">缴存明细</mt-tab-item>
        <mt-tab-item id="2">发放记录</mt-tab-item>
    </mt-navbar>
<!-- tab-container -->
    <mt-tab-container v-model="selected" v-for="(item,index) in usermsg" :key="index">
        <mt-tab-container-item id="1">
            <div class="newsview">
                <ul class="resultList">
                    <li>
                        <div class="resultTop">
                        <h4>{{item.when}}</h4>
                        <div class="resultinfo">
                            <ul>
                            <li>
                                <span>{{item.personerPay}}</span>
                                <span>个人缴纳</span>
                            </li>
                            <li>
                                <span>{{item.companyPay}}</span>
                                <span>单位缴纳</span>
                            </li>
                            <li>
                                <span>{{item.sum}}</span>
                                <span>合计（元）</span>
                            </li>
                            </ul>
                        </div>
                        </div>
                        <div class="resultBottom" style="display:none;">
                        <div class="resultBottom1">
                            <span>缴纳基数</span>
                            <span>{{item.basePay}}</span>
                        </div>
                        <div class="resultBottom2">
                            <span>参保单位</span>
                            <span>{{item.participatingUnits}}</span>
                        </div>
                        </div>
                        <div class="show">
                        <img class="showimg" src="../assets/down.png" alt="">
                        </div>
                    </li>
                </ul>
                
            </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
            <span>暂无此类信息</span>
        </mt-tab-container-item>
    </mt-tab-container>
    </div>
  </div>
</template>

<script>
import '../lib/js/jquery-1.11.0.min.js'
$(document).ready(function(){
    $(document).on("click",".show",function(){
      if(!$(this).children().hasClass('toggleimg')) {
        $(this).children().addClass('toggleimg')
        $(this).prev().css({
          "display":"block"
        });
      }else {
        $(this).children().removeClass('toggleimg')
        $(this).prev().css({
          "display":"none"
        });
      }
  })
})
export default {
  name: 'losejob',
  data () {
    return {
      msg: '失业保险',
      isshow:true,
      isshow2:false,
      selected:'1',
      show:false,
      usermsg:''
    }
  },
  methods:{
        toggle() {
            this.isshow = !this.isshow
            this.isshow2 = !this.isshow2
        },
        
},
created(){

    // var name=   this.$store.idnumber
    // var id= this.$store.password
    var realname = localStorage.getItem("realname")
    var cardid = localStorage.getItem("cardid")
    this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/losejobinsurancestore.do', {
        params: {
            // idnumber:cardid,
            // name:realname,//这里传登录过后的用户名

            idnumber:"159357",
            name:"张三",//这里传登录过后的用户名
            } 
        })
          .then( res => {
            var data = res.data
            this.usermsg = data
            // console.log(this.usermsg)
            localStorage.removeItem("realname")
            localStorage.removeItem("cardid")
        })
          .catch(function (error) {
          console.log(error);
        });
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
     /* 缴存明细与提取明细 导航*/
    .nav {
        padding: 10px;
    }
    .mint-navbar {
        height: 34px;
    }
    .mint-tab-item {
        border-right: 1px solid #00558e!important;
        line-height: 30px;
        border: 1px solid #00558e;
        width: 50%;
        height: 30px;
        padding: 0;
        color: #00558e;
        /* border-radius: 2.5px; */
    }
    /* 缴存明细分页 */
        .newsview {
        padding: 2px;
    }
    .newsview .resultList {
        width: 100%;
        box-sizing: border-box;
    }
    .newsview .resultList li {
        margin: 0;
        width: 93%;
        border: 1px solid #e7e7e7;
        padding: 10px;
    }
    .resultTop h4 {
        margin-top: 0;
        font-weight: 100;
        font-size: 15px;
    }
    .resultinfo {
        overflow: hidden;
    }
    .resultTop .resultinfo ul li span:nth-of-type(1) {
        color: #FED517;
    }
    .resultTop .resultinfo ul li {
        width: 32%;
        float: left;
        border: none;
        text-align: center;
        font-size: 14px;
        color: #999;
        padding: 0;
    }
    .resultTop .resultinfo ul li span {
        display: block;
        margin: 10px 0 5px 0;
    }
    .resultBottom {
        border-top: 1px solid #e7e7e7;
        margin-top: 10px;
    }
    .resultBottom span {
        font-size: 14px;
        color: #999;
        display: inline-block;
        margin: 6px 0 6px 0;
    }
    
    .resultBottom .resultBottom1 {
        margin: 6px 0 6px 0;
    }
    .resultBottom .resultBottom2 {
        margin: 6px 0 6px 0;
    }
    .resultBottom .resultBottom1 span:nth-of-type(2) {
        float: right;
    }
    .resultBottom .resultBottom2 span:nth-of-type(2) {
        float: right;
    }
    .show {
        width: 100%;
        height: 16px;
    }
    .show img {
        display: block;
        width: 16px;
        height: 16px;
        margin: 0 auto;
    }
    .toggleimg {
        transform: rotate(180deg)
    }
</style>



